.App {
    padding: 5%;
    text-align: center;
    font-size: 16px;
}
.party {
    width: 700px;
    height: 400px;
    margin: 0 auto;
}

.fade {
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    -webkit-animation-duration:  2s;
            animation-duration:  2s;
}

.age-stats {
    opacity: 100;
}

@-webkit-keyframes fade-in {
    from { opacity: 0}
    to {opacity: 100}
}

@keyframes fade-in {
    from { opacity: 0}
    to {opacity: 100}
}


